// 商城-菜单管理相关接口
import axios from './../fetch'

// 菜单不分页数据
export const menuManagerList = params => { return axios.get('/admin/platform/menuManager/list', { params: params }) }

// 菜单树形结构
export const menuManagerFindAll = params => { return axios.get('/admin/store/storeUserrole/tree', { params: params }) }
// 菜单信息获取
export const menuManagereGet = params => { return axios.get('/admin/platform/menuManager/get', { params: params }) }

// 菜单删除
export const menuManagerDelete = params => { return axios.post('/admin/platform/menuManager/delete', params) }

// 新增子节点
export const menuManagerSaveChild = params => { return axios.post('/admin/platform/menuManager/saveChild', params) }

export const menuManagerUpdate = params => { return axios.post('/admin/platform/menuManager/update', params) }

// 验证路由节点
export const checkRouting = params => { return axios.get('/admin/platform/menuManager/checkRouting', { params: params }) }

// 验证路由节点
export const menuManagerSort = params => { return axios.post('/admin/platform/menuManager/sort', params) }

export const menuManagerHide = params => { return axios.post('/admin/platform/menuManager/hide', params) }

export const menuManagerShow = params => { return axios.post('/admin/platform/menuManager/show', params) }



